.carrousel {
  display: flex;
  width: 100%;
  height: 500px;
}

.image {
  width: 0px;
  flex-grow: 1;
  object-fit: cover;
  opacity: 0.8;
  transition: 0.5s ease;
}
.image:hover {
  cursor: zoom-in;
  width: 300px;
  opacity: 1;
  filter: contrast(120%);
}
@media screen and (max-width: 900px) {
  .carrousel {
    display: none;
  }
}
